import { MAX_NAME_LENGTH, NonEmptyString } from './BaseString';

export class Name extends NonEmptyString<'Name', string> {
  static from(value: string): Name {
    return new Name(value);
  }
  protected validate(value: string): string {
    value = super.validate(value);

    if (value.length > MAX_NAME_LENGTH) {
      throw new Error(`Name has maximum length of ${MAX_NAME_LENGTH}`);
    }

    return value;
  }
}
